export interface IAISuggestions {
    suggestions: IDictionary<ISuggestionCollapse>;
    errors: ISuggestionError;
    timer?: number,
    hubster?: {
        correlationid: string,
        logid: string,
        totalRequests: number,
        layoutSuggestionRequestMap: Record<number, { suggestionid: number, valid: boolean, appliedByUser?: boolean, undoByUser?: boolean }>
    }
}

export interface IDictionary<TValue> {
    [key: string]: TValue
}

export interface ISuggestionError {
    messages?: Array<string>;
    layoutListLength?: number;
    failCount?: number;
}

export interface IStyleSuggestionForUI {
    componentID: string,
    componentEID: string,
    type: "LAYOUT",
    styleBreakPoint?: string
    name: string,
    valueToBe: string,
    valueAsIs: string,
    description: string,
    isApply: boolean,
    suggestionid: number,
    requestid: number,
    valid?: boolean
}
export interface ISuggestionCollapse {
    isActive?: boolean;
    children: Array<IStyleSuggestionForUI>;
    compEID: string;
}

export interface ISuggestion {
    [componentID: string]: ISuggestionComp
}
export interface ISuggestionComp {
    T: string,
    S: Array<IStyleSuggestion> ,
    eID: string
}
export interface IStyleSuggestion {
    StyleName:string,
    StyleValueToBe: string,
    StyleValueAsIs: string,
    Breakpoint: "xs-xl" | "xs-sm" | "sm-md" | "lg-xl",
    ReasonToChange: string,
    ConfirmationScore:number,
    SuggestionId: number
}
